let loggedIn = false;
let setPassword = 'beaumont2020';

export default{
    login(password){
        return new Promise((resolve, reject) => {
            if (password === setPassword){
                loggedIn = true;
                resolve();
            } else{
                reject();
            }
        });
    },
    logout(){
        loggedIn = false;
    },
    isAuthenticated(){
        return loggedIn;
    }
};