<template>
    <div class="login">
        <div class="wrapper">
            <h2>Enter Password to Continue</h2>
            <form class="form" @submit.prevent="onSubmit" :class="{error}">
                <label>Password</label>
                <input type="text" v-model="password" placeholder="Password" id="myPassword"><br/>
                <button type="submit">Enter</button>
            </form>
            <div class="errorMessage">Hmm that's not right. Try again.</div>
        </div>
    </div>
</template>

<script>
import authAPI from '../auth';

export default{
    name:'Login',
    data: () => ({
        password: null,
        error: false,
    }),
    methods:{
        onSubmit(){
            authAPI.login(this.password).then(() => this.$router.push('/coverletter')).catch(() => { 
                document.querySelector('.errorMessage').style.display = 'block';
                setTimeout(function(){ document.querySelector('.errorMessage').style.display = 'none';  document.getElementById('myPassword').value =''; }, 2000); 
                this.error = true;
            });
        },
    },
};

</script>

<style>
    .errorMessage{
        display: none;
        padding-top: 30px;
    }
    h2{
          text-align: center;
          font-family: 'Open Sans', 'Gill Sans', sans-serif;
          font-weight: 300;
          padding-right: 30px;
          color: #9e967f;
          padding-bottom: 20px;
    }
    ::placeholder {
        color: #CCC;
    }
</style>