<template>
  <div id="coverletter">
    <div class="wrapper">
          <div class="row">
            <div class="form" v-show="formReady != true">
                        <label>Company</label>
                        <input type="text" id="getCompany" v-on:keyup="updateForm"/><br/><br/>
                        <label>Position</label>
                        <input type="text" id="getPosition"/><br/><br/>
                        <label>{{showCompany}} is of strong interest to me because:</label>
                        <textarea type="text" id="getReason" rows="8" cols="50"></textarea><br/><br/>
                        <button @click="createLetter">Make Cover Letter</button>
            </div>
            <div class="letter" v-show="formReady == true">
                    <div>
                        <p class="resume_text" id="date">date</p>
                        <br>
                        <p class="resume_text">To Whom It May Concern:</p>
                        <br>
                        <p class="resume_text">My name is John Donnelly and I am a Senior Editor with over 25 years of experience. I would like to apply for <span class="vowel">a</span> <span class="position" id="position-one">certain</span> position at <span class="company" id="company-one">company</span>.<br><br><span class="company" id="company-two">company</span> is of strong interest to me because <span class="reason">reason</span> My recent experience includes working as a Director of Magazines for the Society of Petroleum Engineers in which I led a team of dedicated writers and editors. I am an expert at managing staff, delegating work effectively and maintaining an online presence on social-network sites. I can successfully correct errors and inconsistencies in grammar, syntax, capitalization, style, punctuation, abbreviations, font size, styles and spacing. I also possess superb editing, proofreading, writing, evaluating and decision-making skills. I would love to work for
                        <span class="company" id="company-three">company</span> and contribute to your continued success.<br><br>My qualifications include:</p>
                        <ul style="padding-left: 13px;">
                            <li style="padding-top: 13px;" class="resume_bullets">Editing and writing engaging content for online and print platforms</li>
                            <li class="resume_bullets">Successfully moving print publications to digital publications</li>
                            <li class="resume_bullets">Experience supervising and motivating staff to achieve their best work</li>
                            <li class="resume_bullets">A strong desire to learn and take direction when given</li>
                        </ul>
                        <p class="resume_text" style="padding-top: 13px;">In closing, I am genuinely interested in a position with <span class="company" id="company-four">company</span>. I look forward to having the opportunity to speak with you and to discuss my qualifications further. Please contact me at 281.682.6579 and please visit my website (<a href="https://johndonnellyeditor.github.io/johndonnelly/" target="_blank" title="portfolio" class="letterLink">johndonnellyeditor.github.io/johndonnelly</a>) to learn more.</p><br>
                        <p class="resume_text">Thank you very much for your time and consideration.</p>
                        <p class="resume_text">Sincerely,<br>John Donnelly</p>
                    </div>

                    <button id="saveButton" onclick="window.print();">Save as PDF</button>
                </div>

            </div><!--Closing wrapper-->
    </div>
  </div>
</template>

<script>
  //npm install --save jspdf
  //import jsPDF from 'jspdf'

  export default{
    name: 'CoverLetter',
    title: "John Donnelly's Cover Letter",
    data(){
        return{
            formReady: false,
            showCompany: 'Company',
            company: '',
            position: '',
            reason: ''
        }
    },
    methods:{
        createLetter(){

                this.company = document.getElementById('getCompany').value;
                this.position = document.getElementById('getPosition').value;
                this.reason = document.getElementById('getReason').value;

                var monthNames = [
                "January", "February", "March",
                "April", "May", "June", "July",
                "August", "September", "October",
                "November", "December"
                ];
                var date = new Date();
                var day = date.getDate();
                var monthIndex = date.getMonth();
                var year = date.getFullYear();

                document.getElementById("date").innerText = monthNames[monthIndex] + ' ' + day + ', ' + year;

                document.getElementById("company-one").innerText = this.company;
                document.getElementById("company-two").innerText = this.company;
                document.getElementById("company-three").innerText = this.company;
                document.getElementById("company-four").innerText = this.company;

                document.querySelector(".position").innerText = this.position;

                if(this.reason.indexOf('.') !== -1 || this.reason.indexOf('?') !== -1 || this.reason.indexOf('!') !== -1){
                    document.querySelector(".reason").innerText = this.reason;
                } else {
                    document.querySelector(".reason").innerText = this.reason+'.';
                }

                if (this.position.startsWith("A") || this.position.startsWith("E") || this.position.startsWith("I") || this.position.startsWith("O")){
                    document.querySelector(".vowel").innerText = 'an';
                }

            this.formReady = true;

        },
        updateForm(){
              this.showCompany = document.getElementById('getCompany').value;
        }
    }
  }
</script>

