import Vue from "vue";
import Router from "vue-router";
import authAPI from './auth';

import Home from "./views/home.vue";
import Resume from "./views/resume.vue";
import Login from "./views/login.vue";
import CoverLetter from "./views/coverletter.vue";

Vue.use(Router);

var myRoute = '/';
var myURL = '';

export default new Router({
  mode:'history',
  routes:[
    {
      path: myRoute,
      name: 'home',
      component: Home,
      meta: {
        title: 'John Donnelly',
      }
    },
    {
      path: myRoute + myURL + 'resume',
      name: 'resume',
      component: Resume
    },
    {
      path: myRoute + myURL + 'login',
      name: 'login',
      component: Login,
    },
    {
      path: myRoute + myURL +  'coverletter',
      name: 'coverletter',
      component: CoverLetter,
      beforeEnter: (to, from, next) => {
        if (authAPI.isAuthenticated() === false){
          next('/login');
        } else{
          next();
        }
      }
    },
    // { path: '/johndonnelly', redirect: { name: 'home' }},
  ],
});
